
import { AjaxHttp } from '../../../js/ajaxHttp'
import { getYMDFormateTime, isArrayType } from "../../../js/utility.js"
import InlineXNumber from 'vux/src/components/inline-x-number/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import { mapState } from 'vuex'

export default {
name: 'relevanceaproduct',
components: {
InlineXNumber,
XInput
},
data() {
return {
itemlist: [],
totalPrice: 0,
totalRate: 0,
sellTotalPrice: 0,
}
},
computed: {
...mapState([
'array',
'addCustomInfos'
]),
},
beforeRouteLeave(to, from, next) {
this.$store.commit('updateArray', []);
next();
},
mounted() {
for (var i = 0; i < this.array.length; i++) {
var item = this.array[i];
var newItem = {};
newItem.name = item.title;
newItem.id = item.id;
newItem.standard = item.standard;
newItem.suggested_price = parseFloat(item.rightTitle).toFixed(2);
newItem.rate = 0.00;
newItem.sell_price = parseFloat(item.rightTitle).toFixed(2);
newItem.count = 1;
newItem.selected = true;
this.itemlist.push(newItem);
}

this.calculateTotalPrice();
},
methods: {
getList() {
AjaxHttp('POST', url, postParams, this, function(res, wself, success) {



});
},
getSelectImage(selected) {
if (selected) {
return require('../../../assets/selected.png');
}
return require('../../../assets/un_selected.png');
},
change(type, item, index) {
if (type === 'rate') {
var sell_price = item.suggested_price * (100.00 - item.rate) / 100.00;
sell_price = sell_price.toFixed(2);
if (item.sell_price !== sell_price) {
item.sell_price = sell_price;
}
} else if (type === 'count') {
if (item.count === '') {
item.count = 1;
}
} else if (type === 'price') {
if (item.sell_price === '') {
item.sell_price = 0.00;
}
var rate = (item.suggested_price - item.sell_price) / item.suggested_price * 100.00;
rate = rate.toFixed(2);
if (item.rate !== rate) {
item.rate = rate;
}
}
this.calculateTotalPrice();
},
calculateTotalPrice() {
var totalPrice = 0.00;
for (var i = 0; i < this.itemlist.length; i++) {
var item = this.itemlist[i];
if (item.selected) {
var sell_price = item.sell_price === '' ? 0.00 : item.sell_price;
totalPrice = totalPrice + sell_price * item.count;
}
}
this.totalPrice = totalPrice.toFixed(2);
this.totalchange(this.totalRate);
},
totalchange(val) {
var sellTotalPrice = this.totalPrice * (100.00 - this.totalRate) / 100.00;
this.sellTotalPrice = sellTotalPrice.toFixed(2);
},
selectItem(index, item) {
item.selected = !item.selected;
this.calculateTotalPrice();
},
sure() {
var itemlist = [];
var totalAmount = 0;
for (var i = 0; i < this.itemlist.length; i++) {
var item = this.itemlist[i];
if (item.selected) {
totalAmount = totalAmount + parseInt(item.count);
itemlist.push(item);
}
} //去掉没有选择的对象

for (var i = 0; i < this.addCustomInfos.length; i++) {
var bigitem = this.addCustomInfos[i];
for (var j = 0; j < bigitem.length; j++) {
var item = bigitem[j];
if (item.form_type === 'product') {
item.hasContent = true;
item.itemlist = itemlist;
item.totalPrice = this.totalPrice;
item.totalRate = this.totalRate;
item.totalAmount = totalAmount;
item.sellTotalPrice = this.sellTotalPrice;
}
}
}

this.$store.commit('updateAddCustomInfos', this.addCustomInfos);
this.$router.go(-2);
}
}
}




